<script>
import appConfig from '@src/app.config'
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../utils/Util';

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Otoritas Username',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Otoritas Username',
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			alert:false,
			modeltampil: "100",
			datatampil: [],
			modelsearch: "",
			dataotoritaswebsite: [],
			modelotoritaswebsite:"",
			dataotoritas: [],
			modelotoritas:"",
		}
	},
	mounted() {
      console.log("alamat", Utils.Host())
      this.Getdata()
	  this.Getfilter()
    },
	computed: {
      pilihmodeltampil() {
        return this.Getdata(this.modeltampil);
      },
      pilihmodelsearch() {
        return this.Getdata(this.modeltampil, this.modelsearch);
      },
	  pilihmodelotoritaswebsite() {
        return this.Getdata(this.modeltampil, this.modelsearch, this.modelotoritaswebsite);
      },
	  pilihmodelotoritas() {
        return this.Getdata(this.modeltampil, this.modelsearch, this.modelotoritaswebsite, this.modelotoritas);
      },
    },
	methods: {
      async Getdata(tampil, search, otoritaswebsite, otoritas) {
        try {
            let kodetampil = tampil ? tampil : 100;
            let kodesearch = search ? search : "";
			let kodeotoritaswebsite = otoritaswebsite ? otoritaswebsite.f_kode : "";
			let kodeotoritas = otoritas ? otoritas.f_otoritas_id : "";
            let response = await axios.get(this.api+"/otoritasusername/index?search="+kodesearch +
			"&otoritaswebsite=" +
            kodeotoritaswebsite+
			"&otoritas=" +
            kodeotoritas+
            "&tampil=" +
            kodetampil, Utils.authHeader());
            this.data = response.data;
        } catch (err) {
            console.error(err);
        }
      },
      async openEdit(id1,id2) {
          document.location = '/otoritasusernameaction/edit/' + id1 + '/' +id2
      },
      async openDetail(id1,id2) {
          document.location = '/otoritasusernameaction/detail/' + id1 + '/' +id2
      },
	  async Getfilter() {
        try {
			let response = await axios.get(this.api+"/otoritaswebsite/index", Utils.authHeader());
    		this.dataotoritaswebsite = response.data;
			let response2 = await axios.get(this.api+"/otoritas/index", Utils.authHeader());
    		this.dataotoritas = response2.data;
        } catch (err) {
            console.error(err);
        }
      },
      async openRemove(id1, id2) {
		if (confirm('Anda Yakin akan menghapus Data')) {
        const res =  await axios.delete(this.api+"/otoritasusername/delete?f_password_id="+id1+"&f_otoritas_id="+id2, Utils.authHeader());
		this.alert = true
        this.message = res.data.message
        this.Getdata()
		} else {
        
		}
	  }
	}
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									<br>&nbsp;&nbsp;
									<b-button variant="dark" to="/otoritasusernameaction/tambah">Tambah Data</b-button>
									<br>
									<b-alert
											v-model="alert"
											variant="danger"
											dismissible
											>{{ message }}</b-alert
										>
									<br>
									
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Tampil"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modeltampil"
										type="number"
										required
										@click="pilihmodeltampil"
									></b-form-input>
									</b-form-group>

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Data / Username"
										label-for="input-horizontal"
									>
									<b-form-input
										id="input-1"
										v-model="modelsearch"
										type="text"
										required
										@click="pilihmodelsearch"
									></b-form-input>
									</b-form-group>


									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Nama Website"
										label-for="input-horizontal"
									>
									<b-form-select v-model="modelotoritaswebsite" @click="pilihmodelotoritaswebsite">
										<option v-for='(value, key) in dataotoritaswebsite' :value='value'>{{value.f_nama}}</option>
									</b-form-select>
									</b-form-group>	

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Otoritas"
										label-for="input-horizontal"
									>
									<b-form-select v-model="modelotoritas" @click="pilihmodelotoritas">
										<option v-for='(value, key) in dataotoritas' :value='value'>{{value.f_otoritas}} - {{value.fn_otoritas_website}}</option>
									</b-form-select>
									</b-form-group>	

									<br>
									<div class="table-responsive">
									<table class="table mb-0">
										<thead>
										<tr>
											<th scope="col">No</th>
											<th scope="col">Id</th>
											<th scope="col">Username</th>
											<th scope="col">Nama</th>
											<th scope="col">Status</th>
											<th scope="col">Otoritas</th>
											<th scope="col">Nama Website</th>
											<th scope="col">Action</th>
										</tr>
										</thead>
										<tbody>
										<tr v-for="(tampil,index) in data" :key="tampil.f_password_id">
											<th scope="row">{{index+1}}</th>
											<td>{{tampil.f_password_id}}</td>
											<td>{{tampil.f_username}}</td>
											<td>{{tampil.f_nama}}</td>
											<td>{{tampil.f_statusaktif == '1' ? 'Tidak Aktif' : 'Aktif' }}</td>
											<td>{{tampil.f_otoritas}}</td>
											<td>{{tampil.fn_otoritas_website}}</td>
											<td>
											<b-button variant="info" @click="openDetail(tampil.f_password_id, tampil.f_otoritas_id)">Detail Data</b-button>
											&nbsp;
											<b-button variant="warning" @click="openEdit(tampil.f_password_id, tampil.f_otoritas_id)">Edit Data</b-button>
											&nbsp;
											<b-button variant="danger" @click="openRemove(tampil.f_password_id, tampil.f_otoritas_id)">Delete Data</b-button>
											</td>
										</tr>
										</tbody>
									</table>
									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
